'use client'

import { Eye, Pencil } from 'lucide-react'

type User = {
  id: number
  username: string
  name: string | null
  email: string | null
  nickname: string | null
  phone: string | null
  mobile: string | null
  title: string | null
  role: string | null
  active: number | null
  roles?: string[]
}

type Props = {
  users: User[]
  onView: (user: User) => void
  onEdit?: (user: User) => void
  showActions?: boolean // Controls whether to show edit/view buttons
}

export default function UserTable({ users, onView, onEdit, showActions = true }: Props) {
  return (
    <div className="border border-slate-200 rounded-lg overflow-hidden">
      <table className="w-full">
        <thead className="bg-slate-100">
          <tr>
            <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Username</th>
            <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Name</th>
            <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Email</th>
            <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Title</th>
            <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Status</th>
            {showActions && (
              <th className="text-left px-4 py-3 font-medium text-sm text-slate-700">Actions</th>
            )}
          </tr>
        </thead>
        <tbody>
          {users.length === 0 ? (
            <tr>
              <td colSpan={showActions ? 6 : 5} className="px-4 py-8 text-center text-slate-500">
                No users found
              </td>
            </tr>
          ) : (
            users.map((user) => (
              <tr key={user.id} className="border-t border-slate-200 hover:bg-slate-50 transition-colors">
                <td className="px-4 py-3 font-mono text-sm font-semibold">
                  {user.username}
                </td>
                <td className="px-4 py-3 text-sm">{user.name || '-'}</td>
                <td className="px-4 py-3 text-sm">{user.email || '-'}</td>
                <td className="px-4 py-3 text-sm">{user.title || '-'}</td>
                <td className="px-4 py-3 text-sm">
                  <span
                    className={`px-2 py-1 rounded-full text-xs font-medium ${
                      user.active === 1
                        ? 'bg-green-100 text-green-700'
                        : 'bg-red-100 text-red-700'
                    }`}
                  >
                    {user.active === 1 ? 'Active' : 'Inactive'}
                  </span>
                </td>
                {showActions && (
                  <td className="px-4 py-3">
                    <div className="flex gap-2">
                      <button
                        onClick={() => onView(user)}
                        className="p-2 text-blue-600 hover:bg-blue-50 rounded transition-colors"
                        title="View User"
                      >
                        <Eye size={18} />
                      </button>
                      {onEdit && (
                        <button
                          onClick={() => onEdit(user)}
                          className="p-2 text-green-600 hover:bg-green-50 rounded transition-colors"
                          title="Edit User"
                        >
                          <Pencil size={18} />
                        </button>
                      )}
                    </div>
                  </td>
                )}
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  )
}