'use client'

import { useState, useEffect } from 'react'
import UserTable from '@/components/users/UserTable'
import UserForm from '@/components/users/UserForm'
import { Users as UsersIcon } from 'lucide-react'

type User = {
  id: number
  username: string
  name: string | null
  email: string | null
  nickname: string | null
  phone: string | null
  mobile: string | null
  title: string | null
  role: string | null
  active: number | null
  roles?: string[]
}

export default function UsersPage() {
  const [users, setUsers] = useState<User[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [viewUser, setViewUser] = useState<User | null>(null)
  const [searchTerm, setSearchTerm] = useState('')

  useEffect(() => {
    fetchUsers()
  }, [])

  const fetchUsers = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const res = await fetch('/api/users')
      
      if (!res.ok) {
        throw new Error(`Failed to fetch users: ${res.status}`)
      }
      
      const data = await res.json()
      setUsers(data)
    } catch (error) {
      console.error('Error fetching users:', error)
      setError(error instanceof Error ? error.message : 'Failed to load users')
    } finally {
      setLoading(false)
    }
  }

  const handleView = (user: User) => {
    setViewUser(user)
  }

  const filteredUsers = users.filter(user =>
    user.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email?.toLowerCase().includes(searchTerm.toLowerCase())
  )

  if (loading) {
    return (
      <div className="p-6">
        <div className="animate-pulse">
          <div className="h-8 bg-slate-200 rounded w-1/4 mb-4"></div>
          <div className="h-64 bg-slate-200 rounded"></div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="p-6">
        <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded">
          <p className="font-semibold">Error loading users</p>
          <p className="text-sm">{error}</p>
          <button 
            onClick={fetchUsers}
            className="mt-2 px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
          >
            Retry
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6">
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-slate-800 flex items-center gap-2">
          <UsersIcon size={28} />
          Users Directory
        </h2>
        <p className="text-slate-600 mt-1">View system users (read-only)</p>
      </div>

      <div className="bg-white rounded-lg shadow">
        <div className="p-6">
          {/* Search Bar */}
          <div className="mb-6">
            <input
              type="text"
              placeholder="Search users by name, username, or email..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="w-full px-4 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
            />
          </div>

          {/* Info Banner */}
          <div className="mb-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
            <p className="text-sm text-blue-800">
              <strong>Note:</strong> This is a read-only view. To manage users, assign roles, or reset passwords, 
              please contact an administrator or access the Admin â†’ User Management section if you have permission.
            </p>
          </div>

          {/* Users Count */}
          <div className="mb-4">
            <p className="text-slate-600">
              Showing {filteredUsers.length} of {users.length} users
            </p>
          </div>

          {/* User Table */}
          <UserTable 
            users={filteredUsers} 
            onView={handleView}
            showActions={false}
          />
        </div>
      </div>

      {/* View User Modal (read-only) */}
      {viewUser && (
        <UserForm
          user={viewUser}
          mode="view"
          onClose={() => setViewUser(null)}
        />
      )}
    </div>
  )
}